.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#home_title{
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  background-color: rgb(43, 87, 81);
  height: 100vh;
  background-image: url("./images/IMG_2234.jpg");
  background-repeat: no-repeat;
  background-position-x: center;
  background-size: cover;
}

#title{
  margin-top: 20vh;
}

#home_button{
  text-decoration: none;
  color: black;
}

#home_button:hover{
  font-size: large;
}

#start_home{
  background-color: whitesmoke;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#start_button{
  background-color: whitesmoke;
  color:rgb(213, 143, 212);;
  border: none;
  font-size: large;
}

